.garland{
    display: flex;
    justify-content: center;
    height: 714px;
    width: 100%;
    position: absolute;
    bottom: 55px;
    pointer-events: none;
    z-index: 90;
    overflow: hidden;
}

.garland_hide{
    display: none;
}

.lightrope {
    white-space: nowrap;
    overflow: hidden;
    position: absolute;
    z-index: 1;
    margin: 0;
    padding: 0;
    pointer-events: none;
    border-radius: 50%;
    padding: 0;
    list-style: none;
    margin: 50px auto 0;
}

.lightrope li {
    position: absolute;
    top: 50%;
    left: 50%;
    margin: 0;
    padding: 0;
    list-style: none;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    margin: -5px;
    animation-name: red;
    animation-fill-mode: both;
    animation-iteration-count: infinite;
    animation-duration: 2s;
}

.lightrope li:nth-child(2n + 1) {
    animation-duration: 1.8s;
}

.lightrope li:nth-child(4n + 2) {
    animation-duration: 1.1s;
}

.lightrope li:nth-child(3n + 1) {
    animation-duration: 1.4s;
}

.lightrope li.red { animation-name: red; }
.lightrope li.green { animation-name: green; }
.lightrope li.blue { animation-name: blue; }
.lightrope li.yellow { animation-name: yellow; }

.lightrope li.multicolor:nth-child(1n + 1) { animation-name: red; }
.lightrope li:nth-child(2n + 2).multicolor { animation-name: green; }
.lightrope li:nth-child(3n + 3).multicolor { animation-name: blue; }
.lightrope li:nth-child(4n + 4).multicolor { animation-name: yellow; }

.garland-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 360px;
    height: 110px;
}

.color-btn {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    cursor: pointer;
}

.multicolor-btn {
    background: linear-gradient(
124deg, #ff2400, #e81d1d, #e8b71d, #e3e81d, #1de840, #1ddde8, #2b1de8);
    box-shadow: rgb(0 0 0 / 20%) 0 -1px 7px 1px, inset #441313 0 -1px 9px, #ccc 0 2px 12px;
}

.red-btn {
    background-color: #F00;
    box-shadow: rgb(0 0 0 / 20%) 0 -1px 7px 1px, inset #441313 0 -1px 9px, rgb(255 0 0 / 50%) 0 2px 12px;
}

.blue-btn {
    background-color: #24E0FF;
    box-shadow: rgb(0 0 0 / 20%) 0 -1px 7px 1px, inset #006 0 -1px 9px, #3f8cff 0 2px 14px;
}

.yellow-btn {
    background-color: #FF0;
    box-shadow: rgb(0 0 0 / 20%) 0 -1px 7px 1px, inset #808002 0 -1px 9px, #ff0 0 2px 12px;
}

.green-btn {
    background-color: #ABFF00;
    box-shadow: rgb(0 0 0 / 20%) 0 -1px 7px 1px, inset #304701 0 -1px 9px, #89ff00 0 2px 12px;
}

.onoffswitch {
    position: relative;
    width: 85px;
}
.onoffswitch-checkbox {
    display: none;
}
.onoffswitch-label {
    display: block;
    overflow: hidden;
    cursor: pointer;
    border: 1px solid #999999;
    border-radius: 20px;
}
.onoffswitch-inner {
    width: 200%;
    margin-left: -100%;
    transition: margin 0.3s ease-in 0s;
}
.onoffswitch-inner:before,
.onoffswitch-inner:after {
    float: left;
    width: 50%;
    height: 30px;
    padding: 0;
    line-height: 30px;
    font-size: 16px;
    color: white;
    font-weight: 600;
    box-sizing: border-box;
}
.onoffswitch-inner:before {
    content: "Вкл";
    padding-left: 12px;
    background-color: #237E8F;
    color: #FFFFFF;
}
.onoffswitch-inner:after {
    content: "Выкл";
    padding-right: 10px;
    background-color: #EEEEEE;
    color: #666;
    text-align: right;
}
.onoffswitch-switch {
    width: 26px;
    margin: 2px;
    background: #FFFFFF;
    border: 2px solid #999999;
    border-radius: 20px;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 55px;
    transition: all 0.3s ease-in 0s;
}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner {
    margin-left: 0;
}
.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-switch {
    right: 0px;
}



@keyframes green {
    0%,
    100% {
        background: rgba(0, 247, 165, 1);
        box-shadow: 0px 2px 12px 2px rgba(0, 247, 165, 1);
    }
    50% {
        background: rgba(0, 247, 165, 0.4);
        box-shadow: 0px 2px 12px 2px rgba(0, 247, 165, 0.2);
    }
}
@keyframes blue {
    0%,
    100% {
        background: rgba(0, 255, 255, 1);
        box-shadow: 0px 2px 12px 2px rgba(0, 255, 255, 1);
    }
    50% {
        background: rgba(0, 255, 255, 0.4);
        box-shadow: 0px 2px 12px 2px rgba(0, 255, 255, 0.2);
    }
}
@keyframes red {
    0%,
    100% {
        background: rgba(247, 0, 118, 1);
        box-shadow: 0px 2px 12px 2px rgba(247, 0, 118, 1);
    }
    50% {
        background: rgba(247, 0, 118, 0.4);
        box-shadow: 0px 2px 12px 2px rgba(247, 0, 118, 0.2);
    }
}
@keyframes yellow {
    0%,
    100% {
        background: rgba(255, 255, 0, 1);
        box-shadow: 0px 2px 12px 2px rgba(255, 255, 0, 1);
    }
    50% {
        background: rgba(255, 255, 0, 0.4);
        box-shadow: 0px 2px 12px 2px rgba(255, 255, 0, 0.2);
    }
}