.history_container{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
    gap: 30px;
    max-width: 400px;
    width: 100%;
    overflow-x: hidden;
    padding-left: 5px;
    padding-bottom: 20px;
}

.history_container h2{
    font-size: 20px;
}

.history{
    display: flex;
    gap: 10px;
    height: 140px;
    width: 400px;
    overflow-x: auto;
}

.history::-webkit-scrollbar {	
    height: 10px;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, .5);
    backdrop-filter: blur(6px);}
  
.history::-webkit-scrollbar-thumb {
    background-color: #237E8F;
    border-radius: 5px;
    cursor: pointer;
}

.btn_history{
    max-width: 150px;
    height: 50px;
    border-width: 1px;
}

@media screen and (max-width: 1230px){
    .history_container{
        max-width: 200px;
        width: 100%;
    }
}